import React, { Component } from 'react'
import styled from 'styled-components';
import fetch from 'isomorphic-unfetch';

const StyledExhibitors = styled.div`
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-self: center;
    margin-top: 10rem;
    max-width: ${props => props.theme.maxWidth};


    h1{
        margin: 2rem;
        text-transform: uppercase;
    }

`;

const StyledCard = styled.div`
    display: flex;
    flex-direction: column;

    
`;


export default class ExhibitorList extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             exhibitors: [],
             companies: [],

            
        }
    }
    
 static async getInitialProps() {
  const res = await fetch('https://prelude.eurobrake.net/exhibitors');
  const data = await res.json().catch(error => console.log(error));
  return {
      exhibitors: data 
    };
  };

  componentDidMount() {
     const exhibitors = this.props.exhibitors.exhibitors;
    const company = exhibitors.map(ex => ex.company);
    this.setState({companies: company});
  }
  

    render() {

        return (
            <StyledExhibitors>
                {this.state.companies.map((co, i) => <h1 key={i}>{co}</h1>)}
            </StyledExhibitors>
        )
    }
}

